<template>
  <div id="show-blogs" v-theme:column='"wide"'>
    <h1>博客列表</h1>
    <input type="text" placeholder="搜索" v-model="search">
    <div class="single-blog" v-for="blog in filterBlogs">
<router-link :to="'/blog/'+blog.id"><h2 v-rainbow>{{blog.title | toUppercase|snippet}}</h2></router-link>
<article>{{blog.content}}</article>
    </div>
  </div>
</template>

<script>
  export default {
    name: "ShowBlogs",
    data() {
      return {
        blogs: [],
        search:''
      }
    },
    created() {
      this.$http.get('https://vue-demo-1b8b2.firebaseio.com/posts.json')
        .then(data => {
          return data.json();
        })
        .then(data=>{
          var blogsArray=[];
          for(let key in data){
            data[key].id=key;
            blogsArray.push(data[key])
          }
         this.blogs=blogsArray;
        })
    },
    computed:{
      filterBlogs:function () {
        return this.blogs.filter((blog)=>{
          return blog.title.match(this.search)
        })
      }
    },
    filters:{
      "toUppercase":function (value) {
        return value.toUpperCase();
      }
    }
  }
</script>

<style scoped>
#show-blogs{
  max-width: 800px;
  margin: 0px auto;
}
  .single-blog{
    padding: 20px;
    margin: 20px 0;
    box-sizing: border-box;
    background: #eee;
  }
</style>



