<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form v-if="!submitted">
      <label>博客标题</label><input type="text" v-model="blog.title" required>
      <label>博客内容</label><textarea type="text" v-model="blog.content"></textarea>
      <div id="checkboxes">
        <label>Vue.js</label><input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label>Html</label><input type="checkbox" value="Html" v-model="blog.categories">
        <label>Css</label><input type="checkbox" value="Css" v-model="blog.categories">
        <label>node.js</label><input type="checkbox" value="node.js" v-model="blog.categories">
      </div>
      <label>作者</label>
      <select v-model="blog.author">
        <option v-for="author in authors">{{author}}</option>
      </select>
      <button @click.prevent="postBlog">添加博客</button>
    </form>
    <div v-if="submitted">
      <h3>博客发布成功</h3>
    </div>
    <hr>
    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题:{{blog.title}}</p>
      <p>博客内容:</p>
      <p>{{blog.content}}</p>
      <p>博客分类</p>
      <ul>
        <li v-for="category in blog.categories">{{category}}</li>
      </ul>
      <p>作者:{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
  export default {
    name: "AddBlog",
    data() {
      return {
        blog: {
          title: "",
          content: "",
          categories: [],
          author: ""
        },
        authors: ["henry", "steve", "kiko"],
        submitted: false

      }
    },
    methods: {
      postBlog() {
        this.$http.post("https://vue-demo-1b8b2.firebaseio.com/posts.json",this.blog)
          .then(data => {
            console.log(data);
            this.submitted = true;
          })
      }
    }
  }
</script>

<style scoped>
  #add-blog *{
    box-sizing: border-box;
  }
  #add-blog{
    max-width: 600px;
    margin: 20px auto;
    padding: 20px;
  }
  label{
    display: block;
    margin: 20px 0 10px;
  }
  input[type=text],textarea,select{
    display: block;
    width: 100%;
  }
  #checkboxes label{
    display: inline-block;
  }
  #checkboxes input{
    display: inline-block;
    margin-right: 20px;
    margin-left: 10px;
  }
  button{
    display: block;
    margin: 20px 0;
    background: crimson;
    color: #ffffff;
    border: 0;
    padding: 10px;
    border-radius: 5px;
  }
  #preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin: 30px 0;
  }
  h3{
    margin-top: 10px;
  }
</style>



